import _ from "lodash";

function custom_format(sNum) {
  let sNumParts = sNum.split('.');
  sNumParts[0] = sNumParts[0].replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1,');
  return sNumParts.join('.');
}

export function number_format(value, format) {
  if (!_.isNumber(value))
    return '-';

  if (format === 'N0')
    return custom_format(value.toFixed(0));
  if (format === 'N2')
    return custom_format(value.toFixed(2));
  if (format === 'N4')
    return custom_format(value.toFixed(4));

  if (format === 'P0')
    return (value * 100).toFixed(0) + '%';
  if (format === 'P2')
    return (value * 100).toFixed(2) + '%';
  if (format === 'P4')
    return (value * 100).toFixed(4) + '%';

  return value.toString();
}

export function P2(value) {
  return number_format(value, 'P2');
}

export function P4(value) {
  return number_format(value, 'P4');
}

export function N0(value) {
  return number_format(value, 'N0');
}

export function N2(value) {
  return number_format(value, 'N2');
}

export function N4(value) {
  return number_format(value, 'N4');
}

export { number_format as NF };

//日期newDate 格式化为 09：00
export function Dateformat(patten) {
  if (!patten) return ''
  let hours = patten.getHours();
  let minutes = patten.getMinutes();
  return `${hours}：${minutes}`
}

//日期'08:00 - 09:00' 格式化为 [new Date(2019,08,00),new Date(2019,09,00)]
export function GetDate(patten) {
  if (!patten) return '';
  return _.map(patten.split('-'), item => {
    let hours = item.split('：')[0];
    let minutes = item.split('：')[1];
    let years = new Date().getFullYear();
    let month = new Date().getMonth();
    let day = new Date().getDay();
    hours = hours.trim();
    minutes = minutes.trim();
    return new Date(years, month, day, hours, minutes)
  })
}

// 日期YYYY-MM-DD格式转换为YYYYMMDD
export function DateAnd(date) {
  if (!date) return date;
  if (!_.includes(date, '-')) return date;
  let restStr = '';
  if (date) {
    date = date.toString();
    const timeArr = date.split('-');
    _.forEach(timeArr, (item) => {
      if (item < 10) {
        item = '0' + parseInt(item);
      }
      restStr += item;
    })
  }
  return restStr;
}
